@import url(https://fonts.googleapis.com/css2?family=Bangers&display=swap);
body {
  margin: 0;
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
  background-color: rgba(218, 214, 214, 0.37);
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, '  Courier New',
    monospace;
} */
.dashboard-grid{
  display: grid;
  grid-row-gap: 30px;
  row-gap: 30px;
  grid-template-columns: 0.75fr 1fr;
  grid-template-rows: 1fr;
  margin: 170px auto 30px auto;
  grid-template-areas: 
  "employee ticket"
  "project project"
  ;

}
.login-form{
  display: flex;
  flex-direction: column;
  height: 70%;
  width: 60%;
  margin: 0 auto;
  justify-content: space-around;

}
.dashboard-grid__component-login{
  height: 460px;
  width: 90%;
  margin: 10px auto; 
  text-align: center;
  box-shadow: 5px 5px 10px -5px rgba(107, 97, 97, 0.397);
  background-color: white;
  border-radius: 4px;


}

.dashboard-grid__component-employee{
  height: 650px;
  width: 90%;
  margin: 10px auto; 
  text-align: center;
  box-shadow: 5px 5px 10px -5px rgba(107, 97, 97, 0.397);
  background-color: white;
  border-radius: 4px;
}

.employee-form{
  display: flex;
  flex-direction: column;
   width: 90%;
  margin: 0 auto;  
  background-color: rgba(255, 255, 255, 0.945);
  box-shadow: 0px 0px 5px 3px rgba(107, 97, 97, 0.068);
  border-radius: 4px;
}

.add-proj-form{
  display: flex;
  height: 80px;
  justify-content: space-evenly;
  width: 60%;
  margin: 20px auto;
  box-shadow: 0px 0px 5px 3px rgba(107, 97, 97, 0.068);
  padding: 20px;

}

.add-proj-form__add-employee-dropdown{
  display: flex;
  flex-direction: column;
}

.employee-form__title{
  font-family: 'Bangers', cursive, Helvetica, Arial, sans-serif;
  font-size: 20px;
  margin: 7px 0
}

.popup{
  height: 550px;
  width: 50%;
  margin: 10% auto; 
  text-align: center;
  box-shadow: 5px 5px 10px -5px rgba(107, 97, 97, 0.397);
  background-color: white;
  border-radius: 4px;
}

.popup__form{
display: flex;
flex-direction: column;
margin: 0 auto;
width: 70%;
height: 85%;
justify-content: space-between;
}

.edit-overlay{
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
  /* width and height for the overlay lightgrey background */
  width: 100%;
  height: 100%;
  z-index: 2;
 
}

.dashboard-grid__component-project{
  grid-area: project;
  height: 700px;
  width: 70%;
  margin: 10px auto; 
  text-align: center;
  box-shadow: 5px 5px 10px -5px rgba(107, 97, 97, 0.397);
  background-color: white;
  border-radius: 4px;

}

.dashboard-grid__component-ticket{
  grid-area: ticket;
  height: 425px;
  width: 90%;
  margin: 10px auto; 
  text-align: center;
  box-shadow: 5px 5px 10px -5px rgba(107, 97, 97, 0.397);
  background-color: white;
  border-radius: 4px;

}

.component__topbar{
  height: 50px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 20px;
  width: 90%;
  left: 5%;
  transform: skew(5deg);
  z-index: 1;
  background-image: linear-gradient(to right, rgb(255, 174, 0) ,rgba(255, 186, 58, 0.842));
  box-shadow: 10px 10px 8px -10px #888888;
  border-radius: 2px;
  font-family: 'Bangers', cursive, Helvetica, Arial, sans-serif;
  font-size: 30px;
  color: white
}

.component__topbar--blue{
  background-image: linear-gradient(to right, rgb(0, 68, 255) ,rgba(147, 199, 233, 1));
}

.component__topbar h2{
  transform: skew(-5deg);

}

.close:hover{
  color: cyan
}

.close{
  cursor: pointer;
  align-self: flex-end;
}

.hidden{
  display: none
}

.dashboard-grid__component--hidden{
  display: none;
}

Button:disabled{
  text-decoration: line-through;
  background-color: darkgray;
}

.edit-icon:hover{
  cursor: pointer;
}

.nav-menu--active{
    display: none;
}

.navbar{
    position: fixed;
    top: 0;
    background-color: lightgray;
    height: 10vh;
    width: 100%;
    display: flex;
    align-items: center;
    z-index: 2;
}

.navbar__logo{
    margin-left: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Bangers', cursive, Helvetica, Arial, sans-serif;
    font-size: 30px;
}

.navbar__logout{
    margin-left: 30%
}

.menu-bars, .nav-menu__close {
    margin: 0 15px;
    font-size: 40px;
    background: none;
}

.menu-bars--purple{
    color: rgba(85,26,139,1)
}

.nav-menu{
    background-color: lightgray;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 15%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 700ms;
    z-index: 2;

}

.nav-menu--active{
    left: 0;
    transition: 400ms;
}

.nav-menu__item{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 8px 0 8px 0;
    border-bottom: 1px solid rgba(61, 60, 60, 0.164);
    height: 35px;
    width: 100%

}
.nav-menu__items{
    padding: 0;
    width: 100%
}

.nav-menu__link{
    font-size: 14px;
    text-decoration: none;
    color: purple;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    padding: 0 
}

.nav-menu__item:hover{
    background-color: cyan;
}

.nav-menu__toggle{
    list-style-type: none;
    border-bottom: 1px solid rgba(61, 60, 60, 0.164);

}
.login{
    margin-top: 20vh;
    width: 50%
}

.login-navbar{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-family: 'Bangers', cursive, Helvetica, Arial, sans-serif;
    font-size: 25px;
}

.login-navbar__logo{
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-navbar__button{
    margin-left: 17%;
    margin-right: 5%;
    display: flex;
    width: 300px;
    justify-content: space-evenly;
    align-items: center;
}

.login-navbar__text{
    font-size: 30px;
}

.login-navbar__header{
    margin: 0; 
}

.footer{
    width: 100%;
    height: 500px;
    background-color: #3f51b5;
    margin-top: 100px;
    color: white;
    font-family: 'Bangers', cursive, Helvetica, Arial, sans-serif;
    display: flex;
    justify-content: space-evenly;
    font-size: 25px;
}

div li{
    list-style: none;
    padding: 0;
}

div ul{
    margin: 0
}

div h4{
    text-decoration: underline;
}
.cropped{
    width: 200px;
    height: 200px;
    overflow: hidden;  
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  
}
.cropped__img{
    object-fit: cover;
    margin-top: -30px;
    
}

.img-card{
    box-shadow: 5px 5px 10px -5px rgba(107, 97, 97, 0.397);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    color: black
}

.img-card p, .img-card a{
    font-size: 20px;
}

.trackerfy-info{
    margin: 0 auto;
    width: 50%;
    text-align: center;
    font-family: 'Bangers', cursive, Helvetica, Arial, sans-serif;
    font-size: 25px;

}

.github-link:hover{
    cursor: pointer;
}

